#home {
  @include ftl;
  @include fvcl;
  background: get-color('black');
  height: 100%;
  overflow: hidden;
  padding: 0 10%;
  perspective: 800px;
  width: 100%;

  @include data('state', 'none') {
    pointer-events: none;
    z-index: 0; }

  @include data('state', 'active') {
    pointer-events: auto;
    z-index: 10; } }
