@include font-face(roboto, '/assets/fonts/Roboto-Light.ttf');
@include font-face(roboto, '/assets/fonts/Roboto-LightItalic.ttf');
@include font-face(roboto, '/assets/fonts/Roboto-Regular.ttf');
@include font-face(roboto, '/assets/fonts/Roboto-Italic.ttf');
@include font-face(roboto, '/assets/fonts/Roboto-Bold.ttf');
@include font-face(roboto, '/assets/fonts/Roboto-BoldItalic.ttf');
@include font-face(roboto-slab, '/assets/fonts/RobotoSlab-Light.ttf');
@include font-face(roboto-slab, '/assets/fonts/RobotoSlab-Regular.ttf');
@include font-face(roboto-slab, '/assets/fonts/RobotoSlab-Bold.ttf');
@include font-face(inconsolata, '/assets/fonts/Inconsolata-Regular.ttf');

@mixin title {
  font-family: roboto-slab;
  font-size: 50px;
  font-weight: 400; }

@mixin h1 {
  font-family: roboto-slab;
  font-size: 36px;
  font-weight: 400; }

@mixin h2 {
  font-family: roboto;
  font-size: 36px;
  font-weight: 400; }

@mixin h3 {
  font-family: roboto;
  font-size: 24px;
  font-weight: 400; }

@mixin h4 {
  font-family: roboto;
  font-size: 18px;
  font-weight: 400; }

@mixin p1 {
  font-family: roboto;
  font-size: 18px;
  font-weight: 300; }

@mixin p2 {
  font-family: roboto;
  font-size: 14px;
  font-weight: 400; }

@mixin p3 {
  font-family: inconsolata;
  font-size: 18px;
  font-weight: 400; }

@mixin a1 {
  font-family: roboto;
  font-size: 18px;
  font-weight: 400; }

@mixin b1 {
  font-family: inconsolata;
  font-size: 14px;
  font-weight: 400; }

@mixin c1 {
  font-family: inconsolata;
  font-size: 400; }

#{select-texts()} {
  @include p1;
  color: get-color('white');
  line-height: 1.8em;

  a {
    @include a1; } }

code {
  @include padding(3px 4px);
  @include c1;
  background: get-color('very-dark-grey') !important;
  border-radius: 3px;
  color: get-color('white') !important;
  white-space: pre-line;
  font-style: normal; }

pre {
  @include overflow(scroll);
  @include padding(1em);
  -webkit-overflow-scrolling: touch;
  border-radius: 3px;
  box-sizing: border-box;
  background: get-color('very-dark-grey') !important;
  color: get-color('white');

  code {
    @include padding(0); } }

blockquote {
  @include padding(40px 30px);
  border: 1px solid get-color('white', .1);

  @include screen('>mobile') {
    @include padding(60px 50px); } }

strong {
  font-weight: 700; }

#{select-headings()} {
  color: get-color('white'); }

#{select-list-items()} {
  @include p1;
  color: get-color('white');
  line-height: 1.8em;

  a {
    @include a1; }

  ol & {
    list-style: decimal; } }

#{select-links()} {
  @include transition(opacity, .3s, ease-out);
  color: get-color('white');
  border-bottom: 1px solid get-color('white', .2);

  @include hwot {
    opacity: .6; } }

ol,
ul {
  @include padding(null null null 40px);

  ol {
    & li {
      list-style: lower-alpha; }

    ol {
      & li {
        list-style: lower-roman; } } } }

h1 {
  @include h1;
  letter-spacing: 1px;
  line-height: 1em;
  text-transform: uppercase;

  @include screen('>mobile') {
    @include title; } }

h2 {
  @include h2;
  letter-spacing: 1px;
  text-transform: uppercase; }

h3 {
  @include h3;
  letter-spacing: 1px;
  text-transform: none; }

h4 {
  @include h4;
  letter-spacing: 1px;
  text-transform: none; }
