@mixin log-padding-h() {
  @include padding(null 30px);

  @include gtmobile {
    @include padding(null 80px); }

  @include gttablet {
    @include padding(null 20%); } }

@mixin log-margin-h() {
  @include margin(null 50px);

  @include gtmobile {
    @include margin(null 25%); }

  @include gttablet {
    @include margin(null 30%); } }

@mixin log-paragraph-space($spacing:1.5em) {
  & + p {
    @include margin($spacing null null null); } }

@mixin log-leading-space($spacing:1.5em) {
  * + &:not(:first-child) {
    @include margin($spacing null null null); } }

@mixin log-trailing-space($spacing:1.5em) {
  @include margin(null null $spacing null); }

#log {
  @include tl;
  -webkit-overflow-scrolling: touch;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  width: 100%;

  #inner-page {
    min-height: 100%;
    min-width: 100%;
    transform: translate3d(0, 0, 0);
    width: 100%; }

  @include name('header') {
    @include display('boxv');
    @include size(100% 100vh);
    @include min-size(_ 480px);
    transform: translate3d(0, 0, 0);

    @include name('info') {
      @include max-size(700px none);
      @include padding(10%);
      @include size(100% auto);

      @include screen('>mobile') {
        @include padding(0); } }

    @include name('title') {
      opacity: 0;
      transform: translate3d(0, 40px, 0);
      hyphens: auto;

      @include screen('>mobile') {
        hyphens: none; } }

    @include name('date') {
      @include display('block');
      @include p3;
      @include margin(10px 0);
      opacity: 0;
      transform: translate3d(0, 40px, 0); }

    @include name('tags') {
      @include display('box>', left center);
      @include margin(10px 0);
      @include size(100% auto);
      opacity: 0;
      transform: translate3d(0, 40px, 0);

      #{select-buttons()} {
        @include space-between(7px); } } }

  @include name('contents') {
    @include padding(200px 0);
    @include log-padding-h();
    @include size(100% auto);
    background: get-color('black');
    opacity: 0;
    transform: translate3d(0, 100px, 0);

    p {
      @include log-paragraph-space(); }

    h2 {
      @include log-leading-space(3em);
      @include log-trailing-space(); }

    h3 {
      @include log-leading-space(2em);
      @include log-trailing-space(1em); }

    h4 {
      @include log-leading-space(2em);
      @include log-trailing-space(1em); }

    pre {
      @include log-trailing-space();
      @include c1;
      @include size(100% auto);

      span.line-numbers-rows {
        margin-top: -3px;
        opacity: .3; }

      code {
        @include c1; } }

    blockquote {
      @include margin(null 10px);
      @include log-leading-space(4em);
      @include log-trailing-space(4em);

      @include screen('>mobile') {
        @include margin(null 30px); } }

    .MathJax_SVG svg *,
    .MathJax_SVG_Display svg * {
      fill: get-color('white'); } }

  @include name('footer') {
    @include display('box>');
    @include margin(-1px null null null);
    @include overflow(hidden);
    @include padding(60px 0);
    @include size(100% auto);
    background: get-color('black');
    transform: translate3d(0, 0, 0);

    #{select-buttons()} {
      @include space-between(30px, '>'); } }

  @include name('related') {
    @include display('boxv', top center);
    @include padding(100px 0);
    @include size(100% auto);
    background: get-color('black', .95);
    transform: translate3d(0, 0, 0); }

  .tags {
    @include display('box>');
    @include margin(0 0 50px 0); }

  .grid {
    @include display('grid>1@85%/270px|30px');
    @include max-size(350px none);

    @include screen('>%s', 650px) {
      @include display('grid>2@100%/270px|30px');
      @include max-size(650px none); }

    @include screen('>%s', 900px) {
      @include display('grid>3@100%/270px|30px');
      @include max-size(900px none); }

    @include element('log-entry') {
      background: get-color('very-dark-grey');

      @include hwot {
        background: get-color('black'); } } } }
