#logs {
  @include tl;
  -webkit-overflow-scrolling: touch;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
  width: 100%;

  #inner-page {
    @include fvtc;
    min-height: 100%;
    min-width: 100%;
    padding: 100px 0;
    width: 100%;

    @include screen('w<=%s' 'h>%s', 650px 1800) {
      @include fvcc; }

    @include screen('w>%s' 'h>%s', 650px 1170px) {
      @include fvcc; }

    @include screen('w>%s' 'h>%s', 900px 870px) {
      @include fvcc; } }

  .tags {
    @include fhcl;
    margin: 0 0 50px 0;
    opacity: 0;
    transform: translate3d(0, -20px, 0);

    #{select-buttons()} {
      @include space-between(10px); } }

  .grid {
    @include display('grid>1@85%/270px|30px');
    @include max-size(350px none);

    @include screen('>%s', 650px) {
      @include display('grid>2@100%/270px|30px');
      @include max-size(650px none); }

    @include screen('>%s', 900px) {
      @include display('grid>3@100%/270px|30px');
      @include max-size(900px none); }

    @include element('log-entry') {
      opacity: 0;
      transform: translate3d(0, 100px, 0); } } }
