@include element('log-entry') {
  @include transition(background, .4s, ease-out);
  background: get-color('dark-grey');

  .wrapper {
    @include padding(40px 30px);
    @include size(100%); }

  #{select-headings()}, #{select-texts()} {
    &.title {
      @include h3;
      color: get-color('white');
      letter-spacing: 0px;
      text-transform: none;
      line-height: 1.26em; }

    &.date {
      @include align(bottom left);
      @include edge(null null 40px 30px);
      @include p3;
      color: get-color('grey'); }

    &.number {
      @include size(auto 80px);
      @include align(bottom right);
      @include edge(null 15px 15px null);
      @include title;
      color: get-color('grey', .2); } }

  @include hwot {
    background: get-color('black'); } }
