body {
  @include overflow(hidden);
  background: get-color('grey');
  perspective: 800px; }

#cover {
  @include align(fixed);
  @include size(100vw 100vh);
  backface-visibility: hidden;
  opacity: 0;
  perspective: 800px;
  transform: translate3d(0, 0, -300px);

  @include name('background') {
    @include align();
    @include size(100%);
    @include transition(transform opacity, .4s, ease-out);
    backface-visibility: hidden;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transform: translate3d(0, 0, 0);
    &::before {
      @include align();
      @include size(100%);
      @include transition(opacity, .3s, ease-out);
      background: get-color('dark-grey');
      content: '';
      opacity: .3; } }

  @include data('state', 'hidden') {
    @include name('background') {
      transform: translate3d(0, 0, -300px);

      &::before {
        opacity: 0; } } } }

#paginator {
  @include align(bottom fixed);
  @include display('box>');
  @include padding(30px);
  @include size(100% auto);
  opacity: 0;
  transform: translate3d(0, 100px, 0);
  z-index: 1000;

  #{select-buttons()} {
    @include margin(null 2px);
    @include transition(opacity transform, .4s, ease-out);
    opacity: .6;
    transform: scale(1);

    @include hwot {
      opacity: 1;
      transform: scale(1.1); }

    @include data('state', 'active') {
      pointer-events: none;
      opacity: 1;
      transform: scale(1.1); }

    &.arrow {
      @include margin(0 10px 0); } } }

#global-nav {
  @include align(fixed);
  @include size(100%);
  z-index: 1000; }

#page {
  @include tl;
  height: 100%;
  overflow: hidden;
  width: 100%; }
