@include element('global-nav') {
  @include pointable(false);

  #{select-buttons()} {
    @include pointable(true); }

  @include name('home-button') {
    @include align(top center);
    @include edge(10px null null null);
    transform: translate3d(0, -100px, 0); }

  @include name('grid-button') {
    @include align(top center);
    @include edge(10px null null null);
    transform: translate3d(0, -100px, 0); }

  @include name('logs-button') {
    @include align(bottom center);
    @include edge(null null 10px null);
    transform: translate3d(0, 100px, 0); }

  @include name('up-button') {
    @include align(top center);
    display: none; }

  @include name('down-button') {
    @include align(bottom center);
    @include edge(null null 10px null);
    display: none; }

  @include name('right-button') {
    @include align(right center);
    @include edge(null 10px null null);
    display: none; }

  @include name('left-button') {
    @include align(left center);
    @include edge(null null null 10px);
    display: none; } }
