#{select-buttons()} {
  &.home {
    @include size(30px);
    svg {
      @include size(100%);
      @include overflow(visible);

      circle {
        @include transition(fill, .6s, ease-out);
        fill: get-color('white');
        transform-origin: center; } }

    @include hwot {
      svg {
        circle {
          fill: get-color('black'); }

        @keyframes dot-pulse {
          0% {
            opacity: 1; }
          50% {
            opacity: 0; }
          100% {
            opacity: 1; } }

        .dot-1 {
          @include animation(dot-pulse, 7s, ease-out, 0s, infinite); }
        .dot-6 {
          @include animation(dot-pulse, 5s, ease-out, .6s, infinite); }
        .dot-8 {
          @include animation(dot-pulse, 3s, ease-out, .8s, infinite); }
        .dot-11 {
          @include animation(dot-pulse, 2s, ease-out, .2s, infinite); } } } }

  &.logs {
    @include size(18px);
    background: transparent;

    #{select-headings()}, #{select-texts()} {
      @include b1;
      @include margin(null null 0px null);
      @include transition(transform opacity, .4s, cubic-bezier(.17,.67,.3,1.23));
      color: get-color('black');
      opacity: 0;
      text-transform: uppercase;
      transform: translate3d(0, 20px 0); }

    svg {
      @include size(100%);

      * {
        @include transition(fill, .3s, cubic-bezier(.17,.67,.3,1.23));
        fill: get-color('white'); } }

    @include hwot {
      #{select-headings()}, #{select-texts()} {
        opacity: 1;
        transform: translate3d(0, 0, 0); }

      svg {
        * {
          fill: get-color('white'); }

        $rotate-duration: 2s;

        .tl {
          @include animation(rotate-tl, $rotate-duration, ease-out, 0s, infinite) {
            @keyframes rotate-tl {
              0% {
                transform: translate3d(0, 0, 0); }
              25% {
                transform: translate3d(35px, 0, 0); }
              50% {
                transform: translate3d(35px, 35px, 0); }
              75% {
                transform: translate3d(0, 35px, 0); }
              100% {
                transform: translate3d(0, 0, 0); } } } }

        .tr {
          @include animation(rotate-tr, $rotate-duration, ease-out, 0s, infinite) {
            @keyframes rotate-tr {
              0% {
                transform: translate3d(0, 0, 0); }
              25% {
                transform: translate3d(0, 35px, 0); }
              50% {
                transform: translate3d(-35px, 35px, 0); }
              75% {
                transform: translate3d(-35px, 0, 0); }
              100% {
                transform: translate3d(0, 0, 0); } } } }

        .bl {
          @include animation(rotate-bl, $rotate-duration, ease-out, 0s, infinite) {
            @keyframes rotate-bl {
              0% {
                transform: translate3d(0, 0, 0); }
              25% {
                transform: translate3d(0, -35px, 0); }
              50% {
                transform: translate3d(35px, -35px, 0); }
              75% {
                transform: translate3d(35px, 0, 0); }
              100% {
                transform: translate3d(0, 0, 0); } } } }

        .br {
          @include animation(rotate-br, $rotate-duration, ease-out, 0s, infinite) {
            @keyframes rotate-br {
              0% {
                transform: translate3d(0, 0, 0); }
              25% {
                transform: translate3d(-35px, 0, 0); }
              50% {
                transform: translate3d(-35px, -35px, 0); }
              75% {
                transform: translate3d(0, -35px, 0); }
              100% {
                transform: translate3d(0, 0, 0); } } } } }

      &::before {
        transform: scale(1); } } }

  &.cancel {
    @include size(20px);
    @include transition(opacity, .3s, ease-out);
    background: transparent;

    @include hwot {
      opacity: .6; } }

  &.arrow {
    @include size(15px);
    @include transition(opacity, .3s, ease-out);
    background: transparent;

    @include hwot {
      opacity: .6 !important; } }

  &.triangle {
    @include size(0px);
    @include transition(opacity, .3s, ease-out);
    background: transparent;
    border: 6px solid transparent;

    @include hwot {
      opacity: .6; }

    #{select-headings()}, #{select-texts()} {
      @include align();
      @include display('boxv');
      @include b1;
      text-transform: uppercase; }

    &.up {
      border-bottom-color: get-color('white');
      transform-origin: bottom center;

      #{select-headings()}, #{select-texts()} {
        @include edge(unset unset -26px -50px);
        @include size(100px auto);
        text-align: center; } }

    &.down {
      border-top-color: get-color('white');
      transform-origin: top center;

      #{select-headings()}, #{select-texts()} {
        @include edge(-26px unset unset -50px);
        @include size(100px auto);
        text-align: center; } }

    &.right {
      border-left-color: get-color('white');
      transform-origin: left center;

      #{select-headings()}, #{select-texts()} {
        @include edge(-13px 12px unset unset);
        @include size(auto 26px);
        text-align: right; } }

    &.left {
      border-right-color: get-color('white');
      transform-origin: right center;

      #{select-headings()}, #{select-texts()} {
        @include edge(-13px unset unset 12px);
        @include size(auto 26px);
        text-align: right; } } }

  &.tag {
    @include padding(0px 12px);
    @include transition(backgorund, .3s, ease-out);
    background: get-color('white');
    border-radius: 4px;

    #{select-headings()}, #{select-texts()} {
      @include b1;
      @include transition(color, .3s, ease-out);
      color: get-color('black'); }

    @include hwot {
      background: get-color('black');

      #{select-headings()}, #{select-texts()} {
        color: get-color('white'); } } }

  &.dot {
    @include size(5px, true);
    background: get-color('white');
    overflow: visible; }

  &.post {
    @include padding(15px 20px);
    @include size(100px auto);
    @include transition(border, .3s, ease-out);
    background: transparent;
    border: 1px solid get-color('white', .1);

    #{select-icons()} {
      @include size(15px); }

    #{select-headings()}, #{select-texts()} {
      color: get-color('white');
      text-transform: capitalize; }

    @include hwot {
      border: 1px solid get-color('white'); }

    &.prev {
      @include display('box<', right center);

      #{select-icons()} {
        @include margin(null null null 10px); } }

    &.next {
      @include display('box>', left center);

      #{select-icons()} {
        @include margin(null 10px null null); } } } }

