#{select-icons()} {
  @include display('(block)');
  @include size(100%);
  background-repeat: no-repeat;
  background-size: 200% 100%;

  &.email {
    background-image: url(/assets/images/common/email-icon.svg); }
  &.twitter {
    background-image: url(/assets/images/common/twitter-icon.svg); }
  &.github {
    background-image: url(/assets/images/common/github-icon.svg); }
  &.variante {
    background-image: url(/assets/images/common/variante-icon.svg); }
  &.verb {
    background-image: url(/assets/images/common/verb-icon.svg); }
  &.mu {
    background-image: url(/assets/images/common/mu-icon.svg); }
  &.cancel {
    background-image: url(/assets/images/common/cancel-icon.svg); }
  &.up {
    background-image: url(/assets/images/common/up-icon.svg); }
  &.prev {
    background-image: url(/assets/images/common/prev-icon.svg); }
  &.next {
    background-image: url(/assets/images/common/next-icon.svg); }
  &.mouse {
    background-image: url(/assets/images/common/mouse-icon.svg); }
  &.grid {
    background-image: url(/assets/images/common/grid-icon.svg); }

  &, &.light {
    background-position: 100% 0; }
  &.dark {
    background-position: 0 0; } }
