@include element('name-card') {
  @include min-size(250px 0);

  section.logo {
    @include padding(10px 0);
    transform: translate3d(0, 0, 0);

    svg {
      @include size(70px);
      @include overflow(visible);
      transform: translate3d(0, 0, 0);

      * {
        fill: get-color('white');
        transform-origin: center; } } }

  section.name {
    #{select-headings()}, #{select-texts()} {
      @include display('(block)');

      &.full-name {
        @include h2;
        letter-spacing: 1px;
        text-transform: uppercase; } } }

  section.position {
    margin: .5em 0 0;

    #{select-headings()}, #{select-texts()} {
      @include display('block');
      @include p2;
      color: get-color('dark-grey');
      line-height: 1.4em;
      text-transform: none; } }

  section.connect {
    @include padding(40px 0 0);

    #{select-buttons()} {
      @include display('(block)');
      @include size(20px);
      margin: 6px;
      background: transparent;

      @include transition(transform opacity, .6s, cubic-bezier(.29,1.48,.24,.96)) {
        @include hwot {
          opacity: .6; } }

      &.inactive {
        pointer-events: none;

        > * {
          opacity: .2; }

        &::after {
          @include cc;
          width: 100%;
          height: 1px;
          content: '';
          background: get-color('light-grey'); } } } } }
