@function get-color($color:null, $alpha:1) {
  @if $color == null {
    @return transparent; }
  @if $color == 'black' {
    @return rgba(26, 26, 26, $alpha); }
  @if $color == 'white' {
    @return rgba(255, 255, 255, $alpha); }
  @if $color == 'light-grey' {
    @return rgba(200, 200, 200, $alpha); }
  @if $color == 'grey' {
    @return rgba(#999, $alpha); }
  @if $color == 'dark-grey' {
    @return rgba(#666, $alpha); }
  @if $color == 'very-dark-grey' {
    @return rgba(#333, $alpha); }
  @if $color == 'blue' {
    @return rgba(#bae1fb, $alpha); }
  @return rgba(#ff0, 1); }
